module.exports = "body{\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.container{\r\n    background-image: linear-gradient(-225deg, #A445B2 0%, #D41872 52%, #FF0066 100%);   \r\n    min-height: 700px;\r\n    padding: 5px 40px 40px 40px;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n    margin-top: 50px;    \r\n}\r\n\r\n.Management{\r\n    font-size: 40px;\r\n    font-family: Agency FB;\r\n    font-weight: 700;\r\n    color:  #FEFDFD;\r\n    border-bottom-style: ridge;\r\n}\r\n\r\n.textName{\r\n    height: 50px;\r\n}\r\n\r\nlabel{\r\n    font-size: 18px;\r\n    color: #FEFDFD;\r\n}\r\n\r\n.table{\r\n    background-color:  #FEFDFD;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n    margin-top: 20px;\r\n\r\n}\r\n\r\n.thead{\r\n    background-color: rgb(254, 255, 255);\r\n}\r\n\r\n/* .btn1,.btn2{\r\n    top: 50%;\r\n    left: 50%;\r\n    background: none;\r\n    color: black;\r\n    width: 120px;\r\n    height: 30px;\r\n    border-radius: 4px;\r\n    transition: .6s;\r\n    overflow: hidden;\r\n} */\r\n\r\n.switch {\r\n    position: relative;\r\n    display: inline-block;\r\n    width: 45px;\r\n    height: 20px;\r\n    vertical-align: middle;\r\n    margin-top: 8px;\r\n  }\r\n\r\n.switch input {display:none;}\r\n\r\n.slider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background-color: rgb(235, 8, 8);\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n  \r\n  }\r\n\r\n.slider:before {\r\n    position: absolute;\r\n    content: \"\";\r\n    height: 16px;\r\n    width: 14px;\r\n    left: 2px;\r\n    bottom: 2px;\r\n    background-color: white;\r\n    -webkit-transition: .4s;\r\n    transition: .4s;\r\n  }\r\n\r\ninput:checked + .slider {\r\n    background-color: rgb(7, 233, 7);\r\n  }\r\n\r\ninput:focus + .slider {\r\n    box-shadow: 0 0 1px #2196F3;\r\n  }\r\n\r\ninput:checked + .slider:before {\r\n    -webkit-transform: translateX(26px);\r\n    transform: translateX(26px);\r\n  }\r\n\r\n/* Rounded sliders */\r\n\r\n.slider.round {\r\n    border-radius: 34px;\r\n  }\r\n\r\n.slider.round:before {\r\n    border-radius: 50%;\r\n  }\r\n\r\n.btn{\r\n    font-weight: 700;\r\n    background-color: transparent;\r\n    border: 1px solid rgba(8, 8, 8, 0.24);\r\n    border-radius: 25px;\r\n    color: #000;\r\n    }\r\n\r\n.btn:hover{\r\n    background-color: #FF0066; \r\n    color: white;\r\n    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"